import BaseIframe from "../../plugin/script/common/BaseIframe";
import InputArea from "../../plugin/components/InputArea";
export default {
  name: "fresher_awards",
  extends: BaseIframe,
  components: {
    InputArea,
  },
  data() {
    return {
      value:'金币',
      formLabelWidth: "120px",
      tabItem: ["新人奖励"],
      currentIndex: 0,
      gold: "",
      // money: "",
      counter: [
        {
         newbie_award: 999,
         bind_phone_award: 10
        }
      ],
      // dialogFormVisible: false,
      // form: {
      //   reward_id: "",
      //   reward_name: "",
      // }
    };
  },
  created() {
    this.initData()
  },
  methods: {
    showTab(index) {
      this.currentIndex = index;
    },

    async save(type){      
      console.log(this.counter);
      
      let {data} = await this.$http.allAgency.PostfresherAward({
        type:type+1,
        value:+this.counter.newbie_award
      });
      console.log(data);
      
    },

    //添加
    // add() {
    //   this.counter.push({item_id:'',count:''});
    // },

    //删除
    // deleteItem(index) {
    //   this.counter = this.counter.filter((item, n) => {
    //     return index !== n;
    //   });
    // },

    //发送到服务器配置
    // sendConfigFile() {
    //   console.log(this.counter);
      
    // },

    async initData(params){
      let {data} = await this.$http.allAgency.GetfresherAward(params)
      console.log(data);
      this.counter = data.data
      
    },
  },
 
};



